import { type InputData, InputDataSchema } from '../config/schema.js';

/**
 * è§£æç»“æœæ¥å£ | Parse result interface
 */
export interface ParseResult {
  success: boolean;
  data?: InputData;
  error?: string;
}

/**
 * ä»stdinè¯»å–è¾“å…¥ | Read input from stdin
 */
export async function readFromStdin(): Promise<string> {
  return new Promise((resolve, reject) => {
    let input = '';

    // è®¾ç½®è¶…æ—¶ | Set timeout
    const timeout = setTimeout(() => {
      reject(new Error('Input timeout'));
    }, 5000);

    process.stdin.setEncoding('utf8');

    process.stdin.on('readable', () => {
      let chunk: string | null;
      // ä½¿ç”¨æ˜¾å¼èµ‹å€¼é¿å…è¡¨è¾¾å¼ä¸­çš„èµ‹å€¼
      chunk = process.stdin.read();
      while (chunk !== null) {
        input += chunk;
        chunk = process.stdin.read();
      }
    });

    process.stdin.on('end', () => {
      clearTimeout(timeout);
      resolve(input);
    });

    process.stdin.on('error', (error) => {
      clearTimeout(timeout);
      reject(error);
    });
  });
}

/**
 * åˆ›å»ºé»˜è®¤è¾“å…¥æ•°æ® | Create default input data
 */
function createDefaultInputData(): InputData {
  return {
    hookEventName: 'Status',
    sessionId: null,
    transcriptPath: null,
    cwd: process.cwd(),
    model: {},
    workspace: {
      current_dir: process.cwd(),
      project_dir: process.cwd(),
    },
    gitBranch: null,
  };
}

/**
 * è§£æJSONè¾“å…¥ | Parse JSON input
 */
export function parseJson(input: string): ParseResult {
  try {
    // å¤„ç†ç©ºè¾“å…¥ | Handle empty input
    if (!input.trim()) {
      return {
        success: true,
        data: createDefaultInputData(),
      };
    }

    // è§£æJSON | Parse JSON
    const rawData = JSON.parse(input);

    // ä½¿ç”¨ZodéªŒè¯å’Œè½¬æ¢æ•°æ® | Use Zod to validate and transform data
    const result = InputDataSchema.safeParse(rawData);

    if (result.success) {
      return {
        success: true,
        data: result.data,
      };
    } else {
      return {
        success: false,
        error: `Input validation failed: ${result.error.message}`,
      };
    }
  } catch (error) {
    // JSONè§£æå¤±è´¥ï¼Œå°è¯•åˆ›å»ºé»˜è®¤æ•°æ® | JSON parse failed, try to create default data
    if (error instanceof SyntaxError) {
      return {
        success: false,
        error: `Invalid JSON: ${error.message}`,
      };
    }

    return {
      success: false,
      error: `Parse error: ${error instanceof Error ? error.message : String(error)}`,
    };
  }
}

/**
 * è§£æè¾“å…¥å¹¶å¤„ç†å®˜æ–¹æ ‡å‡†æ ¼å¼ | Parse input and handle official standard format
 */
export async function parseInput(): Promise<ParseResult> {
  try {
    const input = await readFromStdin();
    return parseJson(input);
  } catch (_error) {
    // stdinè¯»å–å¤±è´¥ï¼Œä½¿ç”¨é»˜è®¤æ•°æ® | stdin read failed, use default data
    return {
      success: true,
      data: createDefaultInputData(),
    };
  }
}

/**
 * éªŒè¯è¾“å…¥æ•°æ®æ ¼å¼ | Validate input data format
 */
export function validate(data: unknown): ParseResult {
  try {
    const result = InputDataSchema.safeParse(data);

    if (result.success) {
      return {
        success: true,
        data: result.data,
      };
    } else {
      return {
        success: false,
        error: `Validation failed: ${result.error.message}`,
      };
    }
  } catch (error) {
    return {
      success: false,
      error: `Validation error: ${error instanceof Error ? error.message : String(error)}`,
    };
  }
}

/**
 * å¤„ç†å‘½ä»¤è¡Œå‚æ•° | Handle command line arguments
 */
export function parseArguments(args: string[]): Partial<InputData> {
  const parsed: Partial<InputData> = {};

  for (let i = 0; i < args.length; i++) {
    const arg = args[i];
    const nextArg = args[i + 1];

    switch (arg) {
      case '--transcript-path':
      case '-t':
        if (nextArg && !nextArg.startsWith('-')) {
          parsed.transcriptPath = nextArg;
          i++;
        }
        break;

      case '--cwd':
      case '-c':
        if (nextArg && !nextArg.startsWith('-')) {
          parsed.cwd = nextArg;
          i++;
        }
        break;

      case '--model':
      case '-m':
        if (nextArg && !nextArg.startsWith('-')) {
          parsed.model = { id: nextArg };
          i++;
        }
        break;

      case '--branch':
      case '-b':
        if (nextArg && !nextArg.startsWith('-')) {
          parsed.gitBranch = nextArg;
          i++;
        }
        break;
    }
  }

  return parsed;
}

/**
 * åˆå¹¶è¾“å…¥æ•°æ® | Merge input data
 */
export function mergeInputData(base: InputData, override: Partial<InputData>): InputData {
  return {
    ...base,
    ...override,
    model: { ...base.model, ...override.model },
    workspace: { ...base.workspace, ...override.workspace },
  };
}

/**
 * è·å–è°ƒè¯•ä¿¡æ¯ | Get debug information
 */
export function getDebugInfo(data: InputData): Record<string, unknown> {
  return {
    hookEventName: data.hookEventName,
    sessionId: data.sessionId,
    transcriptPath: data.transcriptPath,
    cwd: data.cwd,
    model: data.model,
    workspace: data.workspace,
    gitBranch: data.gitBranch,
    env: {
      PWD: process.env.PWD,
      HOME: process.env.HOME,
      USER: process.env.USER,
      TERM: process.env.TERM,
      TERM_PROGRAM: process.env.TERM_PROGRAM,
    },
  };
}

